.tag {
  padding: 12px;
  display: inline-flex;
  align-items: center;
  background-color: $secondary-color;
  color: $primary-color;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
  transition: 0.35s linear all;
  svg {
    width: 32px;
    height: 32px;
  }
  & > * + * {
    margin-left: 4px;
  }
  &:hover {
    @include gradient-animation;
  }
}
